<template>
  <div class="container-fluid">
    
    <div class="column-flex mb-0 pb-0" style="background: #e3e5eb;">
      <div class="card mb-3 sombra">
        <div class="container">
          <div class="row pb-0">
            <div class="col-lg-12 col-md-12 col-sm-12">
              <Modal />
              <TitleCard color="#74569E" dark title="Filtro Alumnos" icon="filter_list"/>
            </div>
          </div>
          <div class="row">
            <div class="col-lg-6 col-md-12 col-sm-12">
              <v-text-field
                color="#74569E"
                v-model="search"
                append-icon="mdi-magnify"
                label="Search"
                single-line
                
                hint="Busca a un alumno por sus datos."
                persistent-hint
              ></v-text-field>
            </div>
            <div class="col-lg-3 col-md-6 col-sm-12">
                <v-combobox
                  v-model="grado"
                  color="#74569E"
                  :items="items"
                  label="Grado"
                  hint="Elige un grado para filtrar."
                  persistent-hint
                ></v-combobox>
            </div>
            <div class="col-lg-3 col-md-6 col-sm-12">
              <v-combobox
                  v-model="seccion"
                  color="#74569E"
                  :items="items"
                  label="Secci贸n"
                  hint="Elige una secci贸n para filtrar."
                  persistent-hint
                ></v-combobox>
            </div>
          </div>
        </div>
      </div>
      </div>
      <div class="card sombra">
        <div class="container">   
          <div >
            <v-data-table
            :headers="alumnos.headers"
            :items="alumnos.desserts"
            :page.sync="page"
            :items-per-page="itemsPerPage"
            hide-default-footer
            @page-count="pageCount = $event"
            :search="search"
          >
            <template v-slot:item.image="{ item }">
              <img class="img-alu" :src="item.image" height="60px" />
            </template>
            <template v-slot:item.action="{ item }">
              <v-icon small class="mr-2" @click="editItem(item)">edit</v-icon>
              <v-icon small @click="deleteItem(item)">delete</v-icon>
            </template>
          </v-data-table>
          </div>
        </div>
      </div>
      <div class="text-center pt-2">
        <v-pagination color="#74569E" v-model="page" :length="pageCount"></v-pagination>
      </div>
    </div>
  
</template>

<script>
import Modal from "./Modal";
import TitleCard from './Cards/TitleCard';
import { mapState, mapActions } from "vuex";
export default {
  components: {
    Modal,
    TitleCard
  },
  data: () => ({
    page: 1,
    pageCount: 0,
    itemsPerPage: 10,
    search: '',
    grado: '',
    seccion: '',
    items: [
      'Primero Primaria',
      'Segundo Primaria',
      'Tercero Primaria',
      'Cuarto Primaria',
      'Quinto Primaria',
    ]
  }),
  computed: {
    ...mapState(["alumnos"])
  },
  mounted(){
    this.$store.dispatch('getAlumnos');
    this.$store.dispatch('updateGlobalIndex');
  }
};
</script>

<style>
/* .lista {
  padding: 15px;
  width: 100%;
} */
.img-alu {
  margin: 10px;
  border-radius: 1em;
  box-shadow: 5px 0px 20px rgba(0, 0, 0, 0.575);
}

.sombra {
  box-shadow: 2px 2px 2px 0px rgba(214,214,214,1);
}
</style>