<template>
  <!-- MODAL -->
  <div class="pt-3" style="background: #fff;">
    <div class="fila mb-3">
      <h4>Lista de Alumnos</h4>

      <Button
      name="Nuevo Alumno"
      addClass="btn-outline-morado"
      :iconAccept="true"
      addIcon="assignment_ind"
      @click="openDialog"
      ></Button>
    </div>
    <hr>
    

    <v-dialog v-model="dialog" max-width="1000px">
      <v-toolbar dense dark color="primary" >
        <v-spacer></v-spacer>
        <v-btn icon dark @click="dialog = false" class="mx-auto">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>

      <v-carousel
        v-model="carousel"
        :continuous="false"
        :cycle="false"
        :show-arrows="false"
        hide-delimiters
        delimiter-icon="mdi-minus"
        height="auto"
        light
        touchless
        
      >
        <v-carousel-item >
          <Form v-on:update:carousel="carousel += $event" v-on:update:dialog="dialog = $event" />
        </v-carousel-item>
        <v-carousel-item>
          <FormParent v-on:update:carousel="carousel += $event" />
        </v-carousel-item>
        <v-carousel-item>
          <FormImage
            v-on:update:carousel="carousel += $event"
            v-on:update:dialog="dialog = $event"
          />
        </v-carousel-item>
      </v-carousel>
    </v-dialog>
  </div>
</template>

<script>
import Button from "./Button";
import Form from "./FormNewUser";
import FormParent from "./FormParentUser";
import FormImage from "./FormImage";

export default {
  components: {
    Button,
    Form,
    FormParent,
    FormImage
  },
  data: () => ({
    dialog: false,
    carousel: 0
  }),
  methods: {
    getAlgo() {
      this.dialog = true;
    },
    openDialog() {
      this.carousel = 0;
      this.dialog = true;
    }
  }
};
</script>

<style>
.fila {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-evenly;
  align-items: center;
}
</style>